.movieCard {
  margin-bottom: 20px;
}
.movieCard .card.body {
  position: relative;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: auto;
  width: 100%;
  padding: 5px;
}
.movieCard img {
  display: block;
  height: 300px;
  margin: auto;
}
.movieCard .card-title {
  text-align: center;
  font-size: small;
}

.movieCard-container {
  padding: 0;
}